body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.container {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.content {
    width: 100%;
    display: flex;
    /* padding: 10px; */
    flex-direction: column;
}

.image > img {
    width: 100%;
    height: 100%;
}

.study-spot-information {
    background-color: #F3F6F9;
    padding: 5px 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.grid {
    width: 80%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 25px;
    gap: 25px;
}

.grid-item {
    grid-row: span 2;
    grid-column: span 1;
    border: 2px solid #888;
    border-radius: 5px;
}

.home-page {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 0px;
    grid-gap: 25px;
    gap: 25px;
}

.add-card {
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    grid-gap: 10px;
    gap: 10px;
}

.add-card > button {
    border: none;
    font-size: 18px;
    padding: 10px 15px;
    border-radius: 5px;
}

@media screen and (max-width: 767px) {
    .grid {
        grid-template-columns: repeat(2, 1fr);
    }
}
.form {
    width: 100%;
    display: flex;
    flex-direction: column;
    grid-gap: 25px;
    gap: 25px;
    align-items: flex-end;
}

.form div {
    display: flex;
    flex-direction: column;
    text-align: left;
    grid-gap: 10px;
    gap: 10px
}

.form div input {
    width: 275px;
}

.form div textarea {
    width: 275px;
}

.form div select {
    width: 275px;
}
.header {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: #E8E8E8;
    height: 125px;
}

.title {
    padding: 10px;
    font-size: 28px;
}

.call-to-action {
    padding: 10px;
}

.call-to-action > button {
    font-size: 18px;
    border: none;
    padding: 10px 15px;
    border-radius: 5px;
}
